import Head from 'next/head'
import Header from '../components/Header'
import Footer from '../components/Footer'
export default ({ children }) => (
  <div>
      <Head>
      <meta name="viewport" content="width=device-width, initial-scale=1" />
      <meta charSet="utf-8" />
    </Head>
    <Header />
    { children }
    <Footer />
  </div>
)